var timeId;
export default {
	data() {
		return {
			imgUrl: '',
			time: 5,
		};
	},
	onLoad() {
		this.getConfig()
	},
	onUnload() {
		clearInterval(timeId)
	},
	methods: {
		getConfig() {
			this.$app.request(this.$app.API.CONFIG, {
				key: 'open_img'
			}, res => {
				if (!res.data) {
					this.$app.goPage('/pages/index/index')
				} else {
					this.imgUrl = res.data
					// 开屏图
					timeId = setInterval(() => {
						if (--this.time <= 0) {
							clearInterval(timeId)
							this.$app.goPage('/pages/index/index')
						}
					}, 1000)
				}

			})
		},
	},
}
